@import "../../../assets/style/mixins.scss";

.container {
  @include us-title-box();
  max-width: 1400px;
  @include visible-translate-X(-500px);
  &:not(:last-child) {
    margin-bottom: 30px;
  }
  &:nth-child(2) {
    margin-top: 40px;
    animation-duration: 1s;
  }
  &:nth-child(3) {
    animation-duration: 1.5s;
  }
  &:nth-child(4) {
    animation-duration: 2s;
    max-width: 800px;
  }
  @media (max-width: 1146px) {
    &:nth-child(4) {
      max-width: 1000px;
    }
  }
  @media (max-width: 450px) {
    &:nth-child(2) {
      margin-top: 30px;
    }
  }
}

.hard__title,
.soft__title,
.lang__title {
  @include us-title();
}

.hard,
.soft,
.lang {
  &__list,
  &__sublist {
    @include reset(li);
  }
}

.hard__list,
.soft__box,
.lang__box {
  overflow: hidden;
}

.hard__item {
  max-width: 650px;
}

.hard__item,
.soft__list,
.lang__list {
  width: 100%;
  min-width: 430px;
  padding: 15px;
  @include transition(box-shadow);
  @include visible-translate-Y(300px);
  &:first-child {
    grid-row: 1/4;
  }
  @media (max-width: 1146px) {
    max-width: 870px;
  }
  @media (max-width: 600px) {
    min-width: 300px;
  }
  @media (max-width: 450px) {
    min-width: 250px;
    padding: 8px;
  }
}

.hard {
  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(430px, 1fr));
    grid-template-rows: auto;
    grid-auto-flow: dense;
    gap: 10px;
    width: 100%;
  }
  &__sub {
    &title {
      margin: 0 0 15px 0;
    }
    &list {
      max-width: inherit;
    }
    &item {
      display: flex;
      flex-direction: column;
      width: 100%;
      max-width: inherit;
    }
  }
  @media (max-width: 600px) {
    &__list {
      grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
    }
  }
  @media (max-width: 450px) {
    &__list {
      grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    }
    &__sub {
      &title {
        margin: 0 0 8px 0;
      }
    }
  }
}

.soft,
.lang {
  &__list {
    padding: 15px;
    @media (max-width: 450px) {
      padding: 8px;
    }
  }
}

.soft {
  &__list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  &__item {
    width: 100%;
    max-width: 425px;
    margin-right: 20px;
  }
  @media (max-width: 1146px) {
    &__item {
      max-width: initial;
      margin-right: 0px;
    }
  }
}
